<template>
  <v-container grid-list-xl fluid>
    <div class="mb-3">
      <h1 class="display-1">  </h1>
    </div>

    <v-layout flex-child wrap>
      <v-flex xs12 md8 class="border">
        <v-flex class="white">
          <!-- Textarea -->
          <v-layout row wrap>
            <v-flex xs12>
              <label> </label>
              <v-textarea
                rows="4"
                solo
                class="border"
                flat
                name="input-7-1"
                label="    "
                v-model="textarea.key_word"
                hide-details
              ></v-textarea>
            </v-flex>

            <!-- <v-flex xs12 xl6>
          <label>  </label>
          <v-textarea
            rows='4'
            solo 
            class="border"
            flat
            name="input-7-1"
            label="     "
            v-model="textarea.minus_key_word"
            hide-details
          ></v-textarea>
            </v-flex>-->
          </v-layout>

          <!-- Checkbox -->
          <v-layout row wrap mx-0 mt-3>
            <v-checkbox
              class="my-0 py-0"
              :label="`   `"
              v-model="checkboxes.exact_phrase"
            ></v-checkbox>
            <v-checkbox
              class="my-0 py-0"
              :label="`  `"
              v-model="checkboxes.only_official"
            ></v-checkbox>
            <v-checkbox
              class="my-0 py-0"
              v-model="checkboxes.with_goods"
              :label="`   `"
            ></v-checkbox>
          </v-layout>

          <v-divider class="mb-3"></v-divider>

          <v-layout wrap>
            <v-flex xs12 md6>
              <label> </label>
              <v-select
                hide-details
                solo
                class="border"
                flat
                v-model="selects.type.selected"
                label="  "
                item-text="title"
                item-value="value"
                :items="selects.type.items"
              ></v-select>
            </v-flex>

            <v-flex xs12 md6>
              <label></label>
              <v-select
                v-model="selects.sort.selected"
                hide-details
                flat
                solo
                class="border"
                item-text="title"
                item-value="value"
                :items="selects.sort.items"
                label="  "
              ></v-select>
            </v-flex>
          </v-layout>

          <label></label>
          <v-layout wrap>
            <v-flex xs6 md3 pt-0>
              <v-text-field
                class="border"
                flat
                solo
                label=""
                v-model="inputs.members.from"
                hide-details
              ></v-text-field>
            </v-flex>

            <v-flex xs6 md3 pt-0>
              <v-text-field
                class="border"
                flat
                solo
                v-model="inputs.members.to"
                label=""
                hide-details
              ></v-text-field>
            </v-flex>

            <v-flex xs6 md3 pt-0>
              <v-autocomplete
                class="border"
                flat
                v-model="selects.country.selected"
                :loading="selects.country.loading"
                :items="selects.country.items"
                :search-input.sync="selects.country.search"
                hide-no-data
                hide-details
                item-text="title"
                item-value="id"
                label=""
                solo
              ></v-autocomplete>
            </v-flex>

            <v-flex xs6 md3 pt-0>
              <v-autocomplete
                class="border"
                flat
                :disabled="!selects.country.selected"
                v-model="selects.city.selected"
                :loading="selects.city.loading"
                :items="selects.city.items"
                :search-input.sync="selects.city.search"
                hide-no-data
                hide-details
                item-text="title"
                item-value="id"
                label=""
                solo
              >
                <template v-slot:item="{ item }">
                  <v-list-tile-avatar
                    color="indigo"
                    class="headline font-weight-light white--text"
                  >{{ item.title.charAt(0) }}</v-list-tile-avatar>
                  <v-list-tile-content>
                    <v-list-tile-title v-text="item.title"></v-list-tile-title>
                    <v-list-tile-sub-title v-text="item.region"></v-list-tile-sub-title>
                  </v-list-tile-content>
                </template>
              </v-autocomplete>
            </v-flex>
          </v-layout>

          <v-divider class="my-4"></v-divider>

          <label> </label>
          <v-layout wrap>
            <v-flex xs12 sm6 md8 pt-0>
              <v-text-field
                class="border"
                v-model="inputs.taskTitle"
                flat
                solo
                label="  ( )"
                hide-details
              ></v-text-field>
            </v-flex>

            <v-flex xs12 sm6 md4 pt-0>
              <v-btn
                class="mt-0"
                style="height:50px;             
            font-size:20px; 
            text-transform:none; 
            background: linear-gradient(160deg,#4f555e,#4f555e,#5c6e68,#7f6b67); 
            color:white"
                block
                flat
                hide-details
                @click="getGroups"
                :loading="!answer"
              >
                <v-icon style="margin-right: 5px">mdi-play</v-icon>
                <label> </label>
              </v-btn>
            </v-flex>
          </v-layout>
        </v-flex>
      </v-flex>

      <v-flex xs12 md4 pt-0>
        <v-flex style="background:#e7fbed;font-size:13px">
          <h1></h1>
          <p>
             " "    Segmento Target,
               10 000  ( 10     API ).
          </p>
          <p>
                    2 ,
                   .
          </p>
          <p>
                 3 ,      
                .   
             ,      
             ,       .
          </p>
          <p>
                .   
                  .    
            --      API  .
          </p>
          <p>
                   ,  ,
              ,    . API   ,
                    .
                       .
            ER=(++)//*100
          </p>
        </v-flex>
      </v-flex>
    </v-layout>
  </v-container>
</template>



<style scoped>
.border {
  border: 1px solid #d7d7d7;
}
</style>



<script>
export default {
  data() {
    return {
      textarea: { search: null, key_word: "", minus_key_word: "" },
      checkboxes: {
        exact_phrase: false,
        only_official: false,
        with_goods: false
      },
      inputs: {
        members: {
          from: null,
          to: null
        },
        taskTitle: null
      },
      selects: {
        country: {
          loading: false,
          items: [],
          selected: null,
          search: null
        },
        city: {
          loading: false,
          items: [],
          selected: null,
          search: null
        },
        type: {
          items: [
            { title: "", value: "group" },
            { title: "", value: "page" },
            { title: "", value: "event" },
            { title: " ", value: "future" }
          ],
          selected: null
        },
        sort: {
          items: [
            {title: " (    )", value: 0},
            { title: "  ", value: 1 },
            { title: "  ", value: 2 },
            { title: "  ", value: 3 },
            { title: "  ", value: 4 },
            { title: "    ", value: 5 }
          ],
          selected: null
        }
      },
      answer: ""
    };
  },
  methods: {
    getGroups() {
      if (!this.textarea.key_word) {
        this.$store.commit("setError", "     ");
        return;
      }
      let obj = {
        q: this.key_phrases,
        type: this.selects.type.selected || 'page',
        city_id: this.selects.city.selected,
        country_id: this.selects.country.selected,
        sort: this.selects.sort.selected || 0,
        members_from: +this.inputs.members.from || 0,
        members_to: +this.inputs.members.to || 99999999,
        exact_phrase: this.checkboxes.exact_phrase,
        verified: this.checkboxes.only_official,
        market: +this.checkboxes.with_goods,
        user_id: this.$store.getters.user.id,
        title: this.inputs.taskTitle || " > "
      };
      this.answer = "";
      this.$http
        .post("/api/search/groups", obj)
        .then(res => {
          this.$store.commit("setSuccess", res.body);
          this.answer = res.body;
        });
    },
    getCities(v) {
      if (!this.selects.country.selected) return;
      this.selects.city.loading = true;
      this.$http
        .get("/api/geolocation/cities", {
          params: {
            q: v,
            country_id: this.selects.country.selected
          }
        })
        .then(res => {
          this.selects.city.items = res.body.items;
          this.selects.city.loading = false;
        });
    },
    getCountries(v) {
      this.selects.country.loading = true;
      this.$http
        .get("/api/geolocation/countries",{
          params:{
            q:v
          }
        })
        .then(res => {
          this.selects.country.items = res.body.items;
          this.selects.country.loading = false;
        });
    }
  },
  watch: {
    "selects.city.search": function(val) {
      val && val !== this.selects.city.selected && this.getCities(val);
    },
    "selects.country.search": function(val) {
      val && val !== this.selects.country.selected && this.getCountries(val);
    }
  },
  computed: {
    key_phrases() {
      return this.textarea.key_word.split`\n`;
    }
  }
};
</script>
