<template>
  <v-container grid-list-xl fluid>
    <div>
      <h1 class="display-1 mb-3"> </h1>
    </div>

    <v-layout flex-child wrap>
      <v-flex xs12 md8 class="border">
        <v-flex class="white">
          <!-- Textarea -->
          <v-layout row wrap>
            <v-flex xs12>
              <label> </label>
              <v-textarea
                rows="4"
                solo
                class="border"
                name="input-7-1"
                label="    "
                flat
                v-model="textarea.value"
                hide-details
              ></v-textarea>
            </v-flex>
          </v-layout>

          <v-layout row wrap mx-0>
            <v-checkbox 
              height="6px" 
              :label="`  `" 
              v-model="checkboxes.hasPhoto"
            ></v-checkbox>

            <v-checkbox
              height="6px"
              :label="`   `"
              v-model="checkboxes.exactMatch"
            ></v-checkbox>
          </v-layout>

          <v-layout wrap>
            <v-flex xs12 sm6>
              <label></label>
              <v-select
                solo
                class="border"
                flat
                label=" "
                :items="selects.sex.items"
                v-model="selects.sex.selected"
                item-text="title"
                item-value="id"
                hide-details
              ></v-select>
            </v-flex>

            <v-flex xs12 sm6>
              <label> </label>
              <v-select
                solo
                class="border"
                flat
                label="  "
                :items="selects.status.items"
                v-model="selects.status.selected"
                item-text="title"
                item-value="id"
                hide-details
              ></v-select>
            </v-flex>
          </v-layout>

          <label></label>
          <v-layout wrap>
            <v-flex xs6 sm3 pt-0>
              <v-text-field
                class="border"
                hide-details
                solo
                flat
                label=""
                v-model="inputs.age.from"
              ></v-text-field>
            </v-flex>

            <v-flex xs6 sm3 pt-0>
              <v-text-field
                class="border"
                hide-details
                solo
                flat
                v-model="inputs.age.to"
                label=""
              ></v-text-field>
            </v-flex>

            <v-flex xs6 sm3 pt-0>
              <v-autocomplete
                class="border"
                flat
                v-model="selects.country.selected"
                :loading="selects.country.loading"
                :items="selects.country.items"
                :search-input.sync="selects.country.search"
                hide-no-data
                hide-details
                item-text="title"
                item-value="id"
                label=""
                solo
              ></v-autocomplete>
            </v-flex>

            <v-flex xs6 sm3 pt-0>
              <v-autocomplete
                class="border"
                flat
                :disabled="!selects.country.selected"
                v-model="selects.city.selected"
                :loading="selects.city.loading"
                :items="selects.city.items"
                :search-input.sync="selects.city.search"
                hide-no-data
                hide-details
                item-text="title"
                item-value="id"
                label=""
                solo
              >
                <template v-slot:no-data>
                  <v-list-tile>
                    <v-list-tile-title>
                      Search for your favorite
                      <strong>Cryptocurrency</strong>
                    </v-list-tile-title>
                  </v-list-tile>
                </template>

                <template v-slot:item="{ item }">
                  <v-list-tile-avatar
                    color="indigo"
                    class="headline font-weight-light white--text"
                  >{{ item.title.charAt(0) }}</v-list-tile-avatar>
                  <v-list-tile-content>
                    <v-list-tile-title v-text="item.title"></v-list-tile-title>
                    <v-list-tile-sub-title v-text="item.region"></v-list-tile-sub-title>
                  </v-list-tile-content>
                </template>
              </v-autocomplete>
            </v-flex>
          </v-layout>

          <!-- <div class="font-weight-medium mt-3"> </div> -->
          <label> </label>
          <v-radio-group v-model="radiogroup.selected" style="margin-top:0px" row>
            <v-radio label="" value="interests"></v-radio>
            <v-radio label="" value="position"></v-radio>
            <v-radio label=" " value="workplace"></v-radio>
            <v-radio label="" value="religion"></v-radio>
            <v-radio label="" value="name"></v-radio>
            <v-radio label="" value="surname"></v-radio>
            <v-radio label=" + " value="name+surname"></v-radio>
            <v-radio label="" value="status"></v-radio>
            <v-radio label=" " value="childname"></v-radio>
          </v-radio-group>

          <v-divider class="mb-4"></v-divider>

          <label> </label>
          <v-layout wrap>
            <v-flex xs12 sm6 md8 pt-0>
              <v-text-field 
                class="border" 
                v-model="inputs.taskTitle"
                flat 
                solo 
                label="  ( )" 
                hide-details
              ></v-text-field>
            </v-flex>

            <v-flex xs12 sm6 md4 pt-0>
              <v-btn
                class="mt-0"
                style="height:50px;             
            font-size:20px; 
            text-transform:none; 
            background: linear-gradient(160deg,#4f555e,#4f555e,#5c6e68,#7f6b67); 
            color:white"
                block
                flat
                hide-details
                @click="getUsers"
                :loading="!answer"
              >
                <v-icon style="margin-right: 5px">mdi-play</v-icon>
                <label> </label>
              </v-btn>
            </v-flex>
          </v-layout>
        </v-flex>
      </v-flex>

      <v-flex xs12 md4 pt-0>
        <v-flex style="background:#e7fbed;font-size:13px">
          <h1></h1>
          <p>
                   ,
            , , ,    ,  ,
             ,       ,
               .
            <br />
            <br />1.    
            <br />
            <br />2.    ,   ,    .
            <br />
            <br />3.  " "    .
            <br />
            <br />  , ,    
               , ..     ,
               ,  ,   .
          </p>
        </v-flex>
      </v-flex>
    </v-layout>
  </v-container>
</template>


<style scoped>
.border {
  border: 1px solid #d7d7d7;
}
</style>


<script>
export default {
  data() {
    return {
      textarea: {
        value: ""
      },

      radiogroup: {
        selected: null
      },

      checkboxes: {
        hasPhoto: false,
        exactMatch: false
      },

      inputs: {
        age: {
          from: null,
          to: null
        },
        taskTitle:null
      },

      selects: {
        sex: {
          items: [
            { title: "", id: 0 },
            { title: "", id: 1 },
            { title: "", id: 2 }
          ],
          selected: null
        },
        status: {
          items: [
            { title: "  /  ", id: 1 },
            { title: "  /  ", id: 2 },
            { title: " / ", id: 3 },
            { title: " / ", id: 4 },
            { title: " ", id: 5 },
            { title: "  ", id: 6 },
            { title: " / ", id: 7 },
            { title: "  ", id: 8 }
          ],
          selected: null
        },
        country: {
          loading: false,
          items: [],
          selected: null,
          search: null
        },
        city: {
          loading: false,
          items: [],
          selected: null,
          search: null
        }
      },
      search: null,
      answer:'1'
    };
  },
  methods: {
    getUsers() {
      //  if (!this.textarea.key_word) {
      //   this.$store.commit("setError", "     ");
      //   return;
      // }
      let obj = {
        q: this.key_phrases,
        searchBy: this.radiogroup.selected,
        exactMatch: this.checkboxes.exactMatch,
        hasPhoto: +this.checkboxes.hasPhoto,
        ageFrom: +this.inputs.age.from,
        ageTo: +this.inputs.age.to,
        status: this.selects.status.selected,
        sex: this.selects.sex.selected,
        city: this.selects.city.selected,
        country: this.selects.country.selected,
        title: this.inputs.taskTitle || " > ",
        user_id: this.$store.getters.user.id
      };
      this.answer = "";
      this.$http.post("/api/search/users", obj).then(res => {
        this.answer = res.body;
      });
    },

    getCities(v) {
      if (!this.selects.country.selected) return;
      this.selects.city.loading = true;
      this.$http
        .get("/api/geolocation/cities", {
          params: {
            q: v,
            country_id: this.selects.country.selected
          }
        })
        .then(res => {
          this.selects.city.items = res.body.items;
          this.selects.city.loading = false;
        });
    },
    getCountries(v) {
      this.selects.country.loading = true;
      this.$http
        .get("/api/geolocation/countries", {
          params: {
            q: v
          }
        })
        .then(res => {
          this.selects.country.items = res.body.items;
          this.selects.country.loading = false;
        });
    } 
  }, // methods
  watch: {
    "selects.city.search": function(val) {
      val && val !== this.selects.city.selected && this.getCities(val);
    },
    "selects.country.search": function(val) {
      val && val !== this.selects.country.selected && this.getCountries(val);
    }
  },
  computed: {
    key_phrases() {
      return this.textarea.value.split`\n`;
    }
  }
};
</script>
